footer .footer-wrapper__links .button {
  border: none;
  font: inherit;
  color: inherit;
  background-color: transparent;
  cursor: pointer;
  display: block;
  position: relative;
  width: 300px;
  height: 78px;
  transition: transform 0.3s ease-in;
  border-radius: 40px;
}
@media (max-width: 992px) {
  footer .footer-wrapper__links .button {
    width: 230px;
    height: 65px;
  }
}
@media (max-width: 576px) {
  footer .footer-wrapper__links .button {
    width: 150px;
    height: 40px;
  }
}
footer .footer-wrapper__links .button .button-background {
  position: absolute;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  display: block;
  background-color: #468217;
  transform-origin: center;
  transition: all 0.2s ease;
  box-shadow: 0px 6px 24px rgba(70, 130, 23, 0);
  z-index: 2;
  border-radius: 40px;
  overflow: hidden;
}
footer .footer-wrapper__links .button .button-background.button-animation::before {
  content: "";
  opacity: 0.7;
  position: absolute;
  top: 0;
  transform: translateX(-125%);
  width: 100%;
  height: 100%;
  background: linear-gradient(100deg, rgba(0, 0, 0, 0), rgba(0, 0, 0, 0) 25%, hsla(0, 0%, 84%, 0.25) 51%, rgba(0, 0, 0, 0) 76%, rgba(0, 0, 0, 0));
  filter: blur(2px);
  transition: opacity 1s ease;
  animation: sheen 3s forwards infinite;
}
footer .footer-wrapper__links .button .button-text {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 100%;
  font-family: "Roboto";
  font-style: normal;
  font-weight: 400;
  font-size: 22px;
  line-height: 100%;
  color: #ffffff;
  z-index: 3;
  position: relative;
}
@media (max-width: 992px) {
  footer .footer-wrapper__links .button .button-text {
    font-size: 18px;
  }
}
@media (max-width: 576px) {
  footer .footer-wrapper__links .button .button-text {
    font-weight: 400;
    font-size: 14px;
    line-height: 100%;
  }
}
footer .footer-wrapper__links .button:not(:disabled):hover .button-background, footer .footer-wrapper__links .button:not(:disabled):focus .button-background {
  transition: all 0.2s ease-in 0.1s;
  background-color: #417915;
  box-shadow: 0px 6px 24px rgba(70, 130, 23, 0.39);
}
footer .footer-wrapper__links .button:not(:disabled):hover .button-background::before, footer .footer-wrapper__links .button:not(:disabled):focus .button-background::before {
  opacity: 0;
}
footer .footer-wrapper__links .button:not(:disabled):hover .button-background {
  transform: scale(0.96);
}
footer .footer-wrapper__links .button:not(:disabled):active {
  transform: translateY(3px);
}
@keyframes sheen {
  100% {
    transform: translateX(100%);
  }
}
footer .footer-wrapper__links .button:disabled .button-background {
  background-color: rgba(95, 95, 95, 0.8784313725);
}

footer {
  background: #2a2a2a;
  padding: 120px 0 170px;
}
@media (max-width: 922px) {
  footer {
    padding: 60px 0 90px;
  }
}
@media (max-width: 768px) {
  footer {
    padding: 45px 0 70px;
  }
}
@media (max-width: 425px) {
  footer {
    padding: 25px 0 60px;
  }
}
footer .footer-wrapper {
  display: grid;
  grid-template-columns: 1fr 1fr;
  row-gap: 30px;
}
@media (max-width: 768px) {
  footer .footer-wrapper {
    grid-template-columns: 1fr;
    gap: 24px;
  }
}
footer .footer-wrapper__logo p {
  margin-top: 8px;
  font-family: "Roboto";
  font-style: normal;
  font-weight: 400;
  font-size: 12px;
  line-height: 14px;
  color: #ffffff;
  opacity: 0.5;
}
@media (max-width: 768px) {
  footer .footer-wrapper__logo p {
    display: none;
  }
}
footer .footer-wrapper__phone {
  grid-column: 2/3;
  grid-row: 1/3;
}
@media (max-width: 768px) {
  footer .footer-wrapper__phone {
    grid-column: 1/2;
    grid-row: 2/3;
  }
}
footer .footer-wrapper__phone a {
  display: block;
  font-family: "Roboto";
  font-style: normal;
  font-weight: 700;
  font-size: 70px;
  line-height: 135%;
  color: #ffffff;
  text-align: right;
  white-space: nowrap;
  font-size: calc(0.0173761946 * 100vw + 36.6377063423px);
}
@media (min-width: 1921px) {
  footer .footer-wrapper__phone a {
    font-size: 70px;
  }
}
@media (max-width: 769px) {
  footer .footer-wrapper__phone a {
    font-size: 50px;
  }
}
@media (max-width: 768px) {
  footer .footer-wrapper__phone a {
    font-weight: 500;
    font-size: 20px;
    line-height: 23px;
    text-align: left;
  }
}
footer .footer-wrapper__phone p {
  text-align: right;
  font-family: "Roboto";
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 19px;
  color: #ffffff;
}
@media (max-width: 768px) {
  footer .footer-wrapper__phone p {
    text-align: left;
    font-size: 12px;
    line-height: 14px;
  }
}
footer .footer-wrapper__decr {
  grid-row: 2/4;
  grid-column: 1/2;
  display: flex;
  flex-direction: column;
  gap: 10px;
  justify-content: center;
}
@media (max-width: 768px) {
  footer .footer-wrapper__decr {
    grid-column: 1/2;
    grid-row: 3/4;
  }
}
@media (max-width: 768px) {
  footer .footer-wrapper__decr {
    gap: 24px;
  }
}
footer .footer-wrapper__decr p {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 140%;
  color: #ffffff;
  white-space: nowrap;
}
@media (max-width: 768px) {
  footer .footer-wrapper__decr p {
    font-size: 12px;
    line-height: 140%;
  }
}
footer .footer-wrapper__decr p span {
  font-weight: 700;
}
footer .footer-wrapper__links {
  grid-row: 3/5;
  grid-column: 2/3;
  display: flex;
  gap: 6px;
  flex-direction: column;
  justify-content: end;
}
@media (max-width: 768px) {
  footer .footer-wrapper__links {
    grid-column: 1/2;
    grid-row: 4/5;
    gap: 3px;
  }
}
footer .footer-wrapper__links a:not(.button) {
  float: right;
  font-family: "Roboto";
  font-style: normal;
  font-weight: 400;
  font-size: 22px;
  line-height: 160%;
  margin-left: auto;
  cursor: pointer;
  color: white;
  position: relative;
  display: inline;
  padding: 5px 0;
  font-size: calc(0.0052128584 * 100vw + 11.9913119027px);
}
footer .footer-wrapper__links a:not(.button)::before, footer .footer-wrapper__links a:not(.button)::after {
  position: absolute;
  content: "";
  left: 0;
  bottom: 5px;
  display: block;
  width: 100%;
  height: 1px;
  background: white;
  transition: 1.1s cubic-bezier(0.19, 1, 0.22, 1);
}
footer .footer-wrapper__links a:not(.button)::before {
  transform: scaleX(0);
  transform-origin: left;
}
footer .footer-wrapper__links a:not(.button)::after {
  transform-origin: right;
  transition-delay: 0.25s;
}
footer .footer-wrapper__links a:not(.button):hover::before {
  transform: scaleX(1);
  transition-delay: 0.25s;
}
footer .footer-wrapper__links a:not(.button):hover::after {
  transform: scaleX(0);
  transition-delay: 0s;
}
@media (min-width: 1921px) {
  footer .footer-wrapper__links a:not(.button) {
    font-size: 22px;
  }
}
@media (max-width: 769px) {
  footer .footer-wrapper__links a:not(.button) {
    font-size: 16px;
  }
}
@media (max-width: 768px) {
  footer .footer-wrapper__links a:not(.button) {
    font-size: 16px;
    line-height: 140%;
    float: left;
  }
}
footer .footer-wrapper__links .button {
  margin-left: auto;
  margin-top: 17px;
}
@media (max-width: 768px) {
  footer .footer-wrapper__links .button {
    margin-right: auto;
    margin-left: 0;
    margin-top: 13px;
  }
}
@media (max-width: 576px) {
  footer .footer-wrapper__links .button {
    width: 145px;
    height: 47px;
  }
}
footer .footer-wrapper__copyright {
  grid-row: 4/5;
  grid-column: 1/2;
}
@media (max-width: 768px) {
  footer .footer-wrapper__copyright {
    grid-column: 1/2;
    grid-row: 5/6;
  }
}
footer .footer-wrapper__copyright p {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 400;
  font-size: 12px;
  line-height: 140%;
  color: #ffffff;
}
@media (max-width: 768px) {
  footer .footer-wrapper__copyright p {
    font-weight: 400;
    font-size: 10px;
  }
}