
@import "../../scss/_global.scss";

footer {
  background: #2a2a2a;
  padding: 120px 0 170px;

  @media (max-width: 922px) {
    padding: 60px 0 90px;
  }

  @media (max-width: 768px) {
    padding: 45px 0 70px;
  }

  @media (max-width: 425px) {
    padding: 25px 0 60px;
  }

  .footer-wrapper {
    display: grid;
    grid-template-columns: 1fr 1fr;
    row-gap: 30px;

    @media (max-width: 768px) {
      grid-template-columns: 1fr;
      gap: 24px;
    }

    &__logo {
      p {
        margin-top: 8px;
        font-family: "Roboto";
        font-style: normal;
        font-weight: 400;
        font-size: 12px;
        line-height: 14px;
        color: #ffffff;
        opacity: 0.5;

        @media (max-width: 768px) {
          display: none;
        }
      }
    }

    &__phone {
      grid-column: 2/3;
      grid-row: 1/3;

      @media (max-width: 768px) {
        grid-column: 1/2;
        grid-row: 2/3;
      }
      a {
        display: block;
        font-family: "Roboto";
        font-style: normal;
        font-weight: 700;
        font-size: 70px;
        line-height: 135%;
        color: #ffffff;
        text-align: right;
        white-space: nowrap;

        @include fluidFontSize(50, 70, 769, 1920);

        @media (max-width: 768px) {
          font-weight: 500;
          font-size: 20px;
          line-height: 23px;
          text-align: left;
        }
      }

      p {
        text-align: right;
        font-family: "Roboto";
        font-style: normal;
        font-weight: 400;
        font-size: 16px;
        line-height: 19px;
        color: #ffffff;

        @media (max-width: 768px) {
          text-align: left;
          font-size: 12px;
          line-height: 14px;
        }
      }
    }
    &__decr {
      grid-row: 2/4;
      grid-column: 1/2;

      @media (max-width: 768px) {
        grid-column: 1/2;
        grid-row: 3/4;
      }

      display: flex;
      flex-direction: column;
      gap: 10px;
      justify-content: center;

      @media (max-width: 768px) {
        gap: 24px;
      }

      p {
        font-family: "Roboto";
        font-style: normal;
        font-weight: 400;
        font-size: 14px;
        line-height: 140%;
        color: #ffffff;
        white-space: nowrap;

        @media (max-width: 768px) {
          font-size: 12px;
          line-height: 140%;
        }

        span {
          font-weight: 700;
        }
      }
    }
    &__links {
      grid-row: 3/5;
      grid-column: 2/3;

      display: flex;
      gap: 6px;
      flex-direction: column;
      justify-content: end;

      @media (max-width: 768px) {
        grid-column: 1/2;
        grid-row: 4/5;
        gap: 3px;
      }

      a:not(.button) {
        float: right;
        font-family: "Roboto";
        font-style: normal;
        font-weight: 400;
        font-size: 22px;
        line-height: 160%;
        margin-left: auto;
        @include linkHover(white);

        @include fluidFontSize(16, 22, 769, 1920);

        @media (max-width: 768px) {
          font-size: 16px;
          line-height: 140%;
          float: left;
        }
      }

      .button {
        margin-left: auto;
        margin-top: 17px;
        @extend %button;

        @media (max-width: 768px) {
          margin-right: auto;
          margin-left: 0;
          margin-top: 13px;
        }

        @media (max-width: 576px) {
          width: 145px;
          height: 47px;
        }
      }
    }
    &__copyright {
      grid-row: 4/5;
      grid-column: 1/2;

      @media (max-width: 768px) {
        grid-column: 1/2;
        grid-row: 5/6;
      }

      p {
        font-family: "Roboto";
        font-style: normal;
        font-weight: 400;
        font-size: 12px;
        line-height: 140%;
        color: #ffffff;

        @media (max-width: 768px) {
          font-weight: 400;
          font-size: 10px;
        }
      }
    }
  }
}
